@import url('https://fonts.googleapis.com/css?family=Poppins:300,400,500,600,700');


/*----------------------------------------------
1. Root
----------------------------------------------*/

/* #region Root */

:root {
    --h1-font: 'Poppins', sans-serif;
    --h1-size: 6rem;
    --h1-weight: 900;
    
    --h2-font: 'Poppins', sans-serif;
    --h2-size: 3rem;
    --h2-weight: 600;
    
    --p-font: 'Poppins', sans-serif;
    --p-size: 1rem;
    --p-weight: 400;

    --header-bg-color: #111111;
    --nav-brand-height: 35px;
    --nav-item-color: #f5f5f5;
    /*--top-nav-item-color: #f5f5f5;*/
    
    --top-nav-item-color: #484848;
    --hero-bg-color: #111111;
    --footer-brand-height: 80px;

    --primary-color: #dd1e4b;
    --secondary-color: #E0040B;
    
    --white-color: #f9f9f9;
    --black-color: #040402;
    --dark-grey-color: #191919;

    --primary-t-color: #2f323a;
    --secondary-t-color: #f5f5f5; 
    --primary-p-color: #6f6f6f;
    --secondary-p-color: #8e8e8e;
    --primary-b-color: #f5f5f5;
    --primary-l-color: rgba(0, 0, 0, .25);
    --secondary-l-color: rgba(255, 255, 255, 0.25);

    --valid-color: #007a4d;
    --invalid-color: #e34f4f;

    --primary-bg-color: #f5f5f5;
    --primary-bg-color-2: #eeeeee;
    --primary-bg-color-3: #e5e5e5;

    --secondary-bg-color: #040402;
    --secondary-bg-color-2: #111111;
    --secondary-bg-color-3: #191919;

    --card-bg-color: #f5f5f5;

    --footer-bg-color: #111111;

}
:root {
    
    --primary-color: #f7941e;
    --secondary-color: #d46103;
    --third-color: #fc751b;

    --section-1-bg-color:#f7941e;
}
/* #endregion Root */

/*----------------------------------------------
1. Reset
----------------------------------------------*/

/* #region Reset */

html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	vertical-align: baseline;
}

article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section {
	display: block;
}

html {
    overflow-x: hidden;
    -webkit-overflow-scrolling: touch;
    -webkit-text-size-adjust: 100%;
}

body {
    width: 100%;
    height: 100%;
    background-color: var(--primary-bg-color);
    font-family: var(--p-font);
    color: var(--primary-p-color);
}

body .body-mode-dark,
body .odd {
    color: var(--secondary-p-color);
}

body.body-mode-dark {
    background-color: var(--secondary-bg-color-2);
    color: var(--secondary-p-color);
}

ol, ul {
    padding-left: 20px;
}

ol {
    list-style: decimal;
}

ul {
    list-style: disc;
}

ul ul {
    list-style: circle;
}

pre, code {
    display: block;
    margin: 1rem 0;
    color: var(--primary-color);
}

blockquote, q {
    display: block;
    margin: 2rem 0;
    quotes: none;
    quotes: none;
    font-style: italic;
    padding: 1rem 1.5rem;
    border-left: 5px solid var(--primary-bg-color-3);
}

.odd blockquote, .odd q,
.body-mode-dark blockquote, .body-mode-dark q {
    border-color: var(--secondary-p-color);
}

blockquote:before, blockquote:after, q:before, q:after {
    content: '';
    content: none;
}

blockquote p, q p {
    margin: 0;
}

.text-center:not(.text-md-left):not(.text-md-right) blockquote {
    width: fit-content;
    margin-left: auto;
    margin-right: auto;
}

figure {
    margin-bottom: 1.5rem;
}

figcaption {
    margin-bottom: 1.5rem;
    font-style: italic;
    padding: 1rem 1.5rem;
    border-left: 5px solid var(--primary-bg-color-3);
}

.odd figcaption,
.body-mode-dark figcaption {
    border-left: 5px solid var(--secondary-bg-color-3);
}

table {
    border-collapse: collapse;
    border-spacing: 0;
}

/* #endregion Reset */
/*----------------------------------------------
2. General
----------------------------------------------*/

/* #region General */

::selection {
    background: rgba(166, 175, 189, 0.3);
}

::-moz-selection {
    background: rgba(166, 175, 189, 0.3);
}


*, *:after, *:before {
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
}

a:not(.btn), a:link:not(.btn), a:visited:not(.btn), a:hover:not(.btn), a:active:not(.btn) {
    font-size: 1rem;
    font-weight: 400;
    line-height: 1.5;
    color: var(--primary-color);
    text-decoration: none;
}

a:not([href]),
a:not([href]):hover,
a:not([href]) i,
a:not([href]) i:hover {
    color: var(--primary-b-color);
}

p {
    margin: 1.5rem 0;
    font-size: var(--p-size);
    font-weight: var(--p-weight);
    line-height: 1.5;
}

p:first-child {
    margin-top: 0;
}

p:last-child {
    margin-bottom: 0;
}

h1,
.slide-content .title {
    margin-top: 0;
    margin-bottom: 1.5rem;
    font-size: var(--h1-size);
    font-weight: var(--h1-weight);
    line-height: 1.2;
    font-family: var(--h1-font);
    color: var(--primary-color);
    -ms-word-wrap: break-word;
    word-wrap: break-word;
}

h1 em,
.slide-content .title em {
    font-weight: 300;
    display: block;
    font-size: var(--h2-size);
}

h2 {
    margin: 1.5rem 0;
    font-size: var(--h2-size);
    font-weight: var(--h2-weight);
    line-height: 1.2;
    font-family: var(--h2-font);
    color: var(--primary-t-color);
    -ms-word-wrap: break-word;
    word-wrap: break-word;
}

h2.featured {
    position: relative;
    width: fit-content;
    margin: auto;
}

.text-md-left h2.featured {
    margin-left: 0;
}

.text-md-right h2.featured {
    margin-right: 0;
}

.text-center.text-center:not(.text-md-left):not(.text-md-right) h2.featured {
    display: flex;
    justify-content: center;
}

.text-center.text-center:not(.text-md-left):not(.text-md-right) h2.featured:before {
    left: auto;
    right: auto;
}

h2.featured:before {
    content: '';
    position: absolute;
    top: -22px;
    left: 0;
    width: 50px;
    height: 5px;
    background-image: -webkit-linear-gradient(45deg, var(--secondary-color) 15%, var(--primary-color) 65%);
    background-image: linear-gradient(45deg, var(--secondary-color) 15%, var(--primary-color) 65%);
}

.odd h2,
.body-mode-dark h2 {
    color: var(--white-color);
}

h2.super {
    margin-bottom: 1.5rem;
    font-size: 4rem;
    font-weight: var(--h1-weight);
    line-height: 1.2;
    font-family: var(--h1-font);
    color: var(--primary-color);
    -ms-word-wrap: break-word;
    word-wrap: break-word;
}

h3 {
    margin: 1.5rem 0;
    font-size: 2rem;
    font-weight: var(--h2-weight);
    line-height: 1.2;
    font-family: var(--h2-font);
    color: var(--primary-t-color);
    -ms-word-wrap: break-word;
    word-wrap: break-word;
}

.odd h3,
.body-mode-dark h3 {
    color: var(--white-color);
}

h4 {
    margin: 1.5rem 0;
    font-size: 1.3rem;
    font-weight: var(--h2-weight);
    line-height: 1.2;
    font-family: var(--h2-font);
    color: var(--primary-t-color);
    -ms-word-wrap: break-word;
    word-wrap: break-word;
}

.odd h4,
.body-mode-dark h4 {
    color: var(--secondary-t-color);
}

h5 {
    margin: 1.5rem 0;
    font-size: 1.2rem;
    font-weight: var(--h2-weight);
    line-height: 1.2;
    font-family: var(--h2-font);
    color: var(--primary-t-color);
    -ms-word-wrap: break-word;
    word-wrap: break-word;
}

.odd h5,
.body-mode-dark h5 {
    color: var(--white-color);
}

h6 {
    margin: 1.5rem 0;
    font-size: 1rem;
    font-weight: var(--h2-weight);
    line-height: 1.2;
    font-family: var(--h2-font);
    color: var(--primary-t-color);
    -ms-word-wrap: break-word;
    word-wrap: break-word;
}

.odd h6,
.body-mode-dark h6 {
    color: var(--white-color);
}

b, strong {
    font-weight: 700;
}

li {
    margin: 0.5rem 0;
    font-size: 1rem;
    font-weight: 400;
    line-height: 1.5;
}

img {
    max-width: 100%;
    height: auto;
    vertical-align: middle;
}

::placeholder {
    color: var(--primary-t-color);
    opacity: 0.5;
}

.odd ::placeholder,
.body-mode-dark ::placeholder {
    color: var(--secondary-t-color);
    opacity: 0.5;
}

table {
    border-collapse: separate;
    border-spacing: 10px;
}

table td {
    padding: 10px 0;
}

table th {
    padding: 10px 0;
}

dt {
    margin: 15px 0;
}

address {
    margin-bottom: 1rem;
}

form {
    width: 100%;
}

form label {
    width: 100%;
}

form label.checkbox {
    width: initial;
}

input, select {
    width: 100%;
    height: 44px;
    background: no-repeat;
    box-shadow: none;
    padding: 0.7rem 1rem;
    border-style: solid;
    border-color: var(--primary-l-color);
    border-width: 1px;
    border-radius: 2px;
}

textarea {
    width: 100%;
    height: auto;
    background: no-repeat;
    box-shadow: none;
    padding: 0.7rem 1rem;
    border-color: var(--primary-l-color);
    border-width: 1px;
    border-radius: 2px;
}

input:focus, textarea:focus, select:focus {
    background: no-repeat;
    color: inherit;
    outline: none;
    border-color: inherit;
    box-shadow: none;
}

input {
    border-color: var(--primary-l-color);
    color: var(--primary-t-color);
}

.odd input,
.body-mode-dark input {
    border-color: var(--secondary-l-color);
    color: var(--secondary-t-color);
}

input:focus {
    border-color: var(--primary-t-color);
    color: var(--primary-t-color);
}

.odd input:focus,
.body-mode-dark input:focus {
    border-color: var(--secondary-l-color);
    color: var(--secondary-t-color);
}

input[type="radio"] {
    height: initial;
}

input[type="submit"] {
    border: 1px solid;
    padding: 2.5px 10px;
    display: block;
    width: fit-content;
    max-width: 200px;
    text-align: center;
    color: var(--primary-color);
}

input[type="submit"]:hover {
    background-color: var(--primary-color);
    color: var(--primary-b-color);
    border-color: var(--primary-color);
}

input[type=checkbox] {
    width: 20px;
    height: 20px;
}

textarea {
    height: 100px;
    resize: none;
    border-color: var(--primary-l-color);
    color: var(--primary-t-color);
}

textarea:focus {
    border-color: var(--primary-t-color);
    color: var(--primary-t-color);
}

.odd textarea,
.body-mode-dark textarea {
    height: 100px;
    resize: none;
    border-color: var(--secondary-l-color);
    color: var(--secondary-t-color);
}

.odd textarea:focus,
.body-mode-dark textarea:focus {
    border-color: var(--secondary-t-color);
    color: var(--secondary-t-color);
}

select {
    -webkit-appearance: none;
    -moz-appearance: none;
    text-indent: 1px;
    text-overflow: '';
    line-height: 1.2;
    border-color: var(--primary-l-color);
    color: var(--primary-t-color);
}

select:focus {
    border-color: var(--primary-t-color);
    color: var(--primary-t-color);
}

select option {
    color: #2f323a;
}

hr {
    margin: 35px 0;
    border-top: 1px dashed var(--primary-l-color);
}

.odd hr,
.body-mode-dark hr {
    margin: 35px 15px;
    border-top: 1px dashed var(--secondary-l-color);
}

table {
    width: 100%;
}

tr:not(:last-child) {
    border-bottom: 1px solid;
    border-color: var(--primary-l-color);
}

.odd tr:not(:last-child),
.body-mode-dark tr:not(:last-child) {
    border-bottom: 1px solid;
    border-color: var(--secondary-l-color);
}

td {
    padding: 10px 0;
}
/*----------------------------------------------
3. Section
----------------------------------------------*/

/* #region Section */

section {
    position: relative;
    width: 100%;
   /* padding: 130px 0;*/
    background-size: cover;
    background-attachment: fixed;
    background-position: center;
}

section.hero       { background-color: var(--hero-bg-color); }
section.section-1  { background-color: var(--section-1-bg-color);  background-image: var(--section-1-bg-image); }
section.section-2  { background-color: var(--section-2-bg-color);  background-image: var(--section-2-bg-image); }
section.section-3  { background-color: var(--section-3-bg-color);  background-image: var(--section-3-bg-image); }
section.section-4  { background-color: var(--section-4-bg-color);  background-image: var(--section-4-bg-image); }
section.section-5  { background-color: var(--section-5-bg-color);  background-image: var(--section-5-bg-image); }
section.section-6  { background-color: var(--section-6-bg-color);  background-image: var(--section-6-bg-image); }
section.section-7  { background-color: var(--section-7-bg-color);  background-image: var(--section-7-bg-image); }
section.section-8  { background-color: var(--section-8-bg-color);  background-image: var(--section-8-bg-image); }
section.section-9  { background-color: var(--section-9-bg-color);  background-image: var(--section-9-bg-image); }
section.section-10 { background-color: var(--section-10-bg-color); background-image: var(--section-10-bg-image); }
section.section-11 { background-color: var(--section-11-bg-color); background-image: var(--section-11-bg-image); }
section.section-12 { background-color: var(--section-12-bg-color); background-image: var(--section-12-bg-image); }
section.section-13 { background-color: var(--section-13-bg-color); background-image: var(--section-13-bg-image); }
section.section-14 { background-color: var(--section-14-bg-color); background-image: var(--section-14-bg-image); }
section.section-15 { background-color: var(--section-15-bg-color); background-image: var(--section-15-bg-image); }

section.section-vs-0   { padding: 0; }
section.section-vs-10  { padding: 10px 0; }
section.section-vs-20  { padding: 20px 0; }
section.section-vs-30  { padding: 30px 0; }
section.section-vs-40  { padding: 40px 0; }
section.section-vs-50  { padding: 50px 0; }
section.section-vs-60  { padding: 60px 0; }
section.section-vs-70  { padding: 70px 0; }
section.section-vs-80  { padding: 80px 0; }
section.section-vs-90  { padding: 90px 0; }
section.section-vs-100 { padding: 100px 0; }
section.section-vs-110 { padding: 110px 0; }
section.section-vs-120 { padding: 120px 0; }
section.section-vs-130 { padding: 130px 0; }
section.section-vs-140 { padding: 140px 0; }
section.section-vs-150 { padding: 150px 0; }
section.section-vs-160 { padding: 160px 0; }
section.section-vs-170 { padding: 170px 0; }
section.section-vs-180 { padding: 180px 0; }
section.section-vs-190 { padding: 190px 0; }
section.section-vs-200 { padding: 200px 0; }

section.featured {
    position: relative;
}

section.featured:after {
    content: '';
    position: absolute;
    z-index: 2;
    bottom: -50px;
    left: 50%;
    left: calc(50% - 2.5px);
    width: 5px;
    height: 100px;
    background-image: -webkit-linear-gradient(45deg, var(--secondary-color) 15%, var(--primary-color) 65%);
    background-image: linear-gradient(45deg, var(--secondary-color) 15%, var(--primary-color) 65%);
}

section.featured.right:before {
    content: '';
    position: absolute;
    z-index: 1;
    top: 100%;
    right: 0;
    width: 50%;
    border-bottom: 1px dashed var(--primary-l-color);
}

section.odd.featured.right:before {
    border-bottom: 1px dashed var(--secondary-l-color);
}

section.featured.left:before {
    content: '';
    position: absolute;
    z-index: 2;
    top: 100%;
    left: 0;
    width: 50%;
    border-bottom: 1px dashed var(--primary-l-color);
}

section.odd.featured.left:before {
    border-bottom: 1px dashed var(--secondary-l-color);
}

section.featured.all:before {
    content: '';
    position: absolute;
    z-index: 2;
    top: 100%;
    left: 0;
    width: 100%;
    border-bottom: 1px dashed var(--primary-l-color);
}

section.featured.right .odd:before {
    content: '';
    position: absolute;
    z-index: 2;
    top: calc(100% - 1px);
    right: 0;
    width: 50%;
    border-bottom: 1px dashed var(--secondary-l-color);
}

section.featured.left .odd:before {
    content: '';
    position: absolute;
    z-index: 1;
    top: calc(100% - 1px);
    left: 0;
    width: 50%;
    border-bottom: 1px dashed var(--secondary-l-color);
}

section.featured.all .odd:before {
    content: '';
    position: absolute;
    z-index: 1;
    top: calc(100% - 1px);
    left: 0;
    width: 100%;
    border-bottom: 1px dashed var(--secondary-l-color);
}

section.odd.featured.all:before {
    border-bottom: 1px dashed var(--secondary-l-color);
}

main {
    padding: 0;
}

aside {
    padding: 0;
}

.container {
    max-width: 1170px;
    padding: 0;
}

.container.header {
    max-width: 1140px;
}

.container.header.full {
    max-width: 100%;
}

.container.header.full-grid {
    max-width: 100%;
    margin: 0 5px;
}

.container.header.smaller {
    max-width: 940px;
}

.container.disabled {
    width: auto;
    max-width: 100%;
}

.container.disabled .content-inner {
    padding: 0;
}

.container.full {
    max-width: 100%;
}

.container.full .col-12 {
    padding: 0;
}

.container.full-grid {
    padding: 0;
    max-width: calc(100% - 30px);
    margin: 0 15px;
}

.container.smaller {
    max-width: 970px;
}

.row {
    margin-left: 0;
    margin-right: 0;
}

.vh-100 {
    height: 100vh;
}

.vh-75 {
    height: 75vh;
}

.clearfix {
    display: block;
    content: "";
    clear: both;
}

.overflow-holder {
    overflow: hidden;
    margin-top: -25px;
    margin-bottom: -25px;
    padding-top: 25px;
    padding-bottom: 25px;
}

/* #endregion Section */

/*----------------------------------------------
4. Helper Class
----------------------------------------------*/

/* #region Helper Class */

.intro {
    margin-bottom: 50px;
}

.intro h1:first-child, .intro h2:first-child, .intro h3:first-child {
    margin-top: 0;
}

.blog-listing .items {
    margin-bottom: 30px;
}

.blog-listing .items .item {
    margin-bottom: 30px;
}

.blog-grid:not(.masonry) .items {
    margin-bottom: 30px;
}

.blog-grid:not(.masonry) .items .item {
    margin-bottom: 30px;
}

.blog-grid:not(.masonry) .items .item.sticky .card:before {
    top: -10px;
    right: 0;
    bottom: 0;
    left: -10px;position: absolute;
    z-index: -1;
    content: '';
    width: calc(50% + 10px);
    height: calc(50% + 10px);
    border: none;
    background-image: -webkit-linear-gradient(45deg, var(--secondary-color) 15%, var(--primary-color) 65%);
    background-image: linear-gradient(45deg, var(--secondary-color) 15%, var(--primary-color) 65%);
    transition-timing-function: cubic-bezier(.25, .25, .75, .75);
    transition-duration: 0.4s;
    transition-property: opacity, transform;
}

.post-holder {
    list-style: none;
    width: 100%;
    border-left: 5px solid var(--primary-bg-color-3);
    background-color: var(--primary-bg-color-2);
    padding: 25px;
    margin-top: 35px;
}

.odd .post-holder,
.body-mode-dark .post-holder {
    border-left: 5px solid var(--primary-color);
    background-color: var(--secondary-bg-color-3);
}

.posted-on a:not(.btn) {
    font-weight: 600;
}

.sidebar .item {
    margin-bottom: 50px;
}

.sidebar .item:last-child {
    margin-bottom: 0;
}

.sidebar .item h4 {
    margin-top: 0;
}

.odd .sidebar .item h4,
.body-mode-dark .sidebar .item h4 {
    color: var(--white-color);
}

.sidebar ol,
.sidebar ul {
    list-style: none;
    padding: 0;
}

.sidebar ol ol, .sidebar ul ul {
    padding: 15px;
}

.sidebar ul.children {
    padding: 0;
}

.sidebar .sub-menu {
    padding: 0;
}

.sidebar ul li:not(.nav-item):not(.list-group-item) {
    position: relative;
    padding-left: 0;
}

.sidebar ul ul li:not(.nav-item):not(.list-group-item) {
    padding-left: 20px;
}

.sidebar li:hover:before {
    background-color: var(--primary-color);
}

.sidebar ul li a:not(.btn) {
    color: inherit;
    overflow-wrap: break-word;
}

.sidebar ul li a:not(.btn):hover {
    color: var(--primary-color);
}

.sidebar .title {    
    width: 100%;
    margin-top: 0;
    list-style: none;
}

