  /* ============= Animation background ========= */
  .background {
    /*background: linear-gradient(132deg, #FC415A, #591BC5, #212335);*/
    background: linear-gradient(132deg, #2e2e2e, #0b0b0c, #0b0b0e);
    background-size: 400% 400%;
    animation: Gradient 15s ease infinite;
    position: relative;
    height: 100vh;
    width: 100%;
    overflow: hidden;
    padding:0;
    margin:0px;
  }

  .background-yellow {
       
    background: linear-gradient(132deg, var(--primary-color), var( --secondary-color), var(-third-color));
    /*background: linear-gradient(132deg, #2e2e2e, #0b0b0c, #0b0b0e);*/
    background-size: 400% 400%;
    animation: Gradient 15s ease infinite;
    position: relative;
    height: 100vh;
    width: 100%;
    overflow: hidden;
    padding:0;
    margin:0px;
  }
  .cube {
    position: absolute;
    top: 80vh;
    left: 45vw;
    width: 10px;
    height: 10px;
    /*border: solid 1px #D7D4E4;*/
    border: solid 1px #1a1a1a;
    transform-origin: top left;
    transform: scale(0) rotate(0deg) translate(-50%, -50%);
    animation: cube 12s ease-in forwards infinite;
  }
  .cube:nth-child(2n) {
    /*border-color: #FFF ;*/
    border: solid 1px #1a1a1a;
  }
  .cube:nth-child(2) {
    animation-delay: 2s;
    left: 25vw;
    top: 40vh;
  }
  .cube:nth-child(3) {
    animation-delay: 4s;
    left: 75vw;
    top: 50vh;
  }
  .cube:nth-child(4) {
    animation-delay: 6s;
    left: 90vw;
    top: 10vh;
  }
  .cube:nth-child(5) {
    animation-delay: 8s;
    left: 10vw;
    top: 85vh;
  }
  .cube:nth-child(6) {
    animation-delay: 10s;
    left: 50vw;
    top: 10vh;
  }
  /* Animate Background*/
@keyframes Gradient {
    0% {
      background-position: 0% 50%;
    }
    50% {
      background-position: 100% 50%;
    }
    100% {
      background-position: 0% 50%;
    }
  }
  @keyframes cube {
    from {
      transform: scale(0) rotate(0deg) translate(-50%, -50%);
      opacity: 1;
    }
    to {
      transform: scale(20) rotate(960deg) translate(-50%, -50%);
      opacity: 0;
    }
  }

  .arrow-bottom{
      position:absolute;
      bottom:50px;
      color: var(--primary-color) !important;
      left:50%;
      right: 50%;
      cursor:pointer;
      z-index: 100;

  }
  .arrow-bottom {
	/*background: var(--primary-b-color);*/
	border-radius: 50%;
	margin: 00px;
	height: 48px;
	width: 48px;

	box-shadow: 0 0 0 0 var(--primary-color);
	transform: scale(1);
	animation: pulse 2s infinite;
    
}

@keyframes pulse {
	0% {
		transform: scale(0.90);
		/*box-shadow: 0 0 0 0 rgba(233, 157, 16, 0.651);*/
	}

	70% {
		transform: scale(1);
		/*box-shadow: 0 0 0 10px  rgba(233, 157, 16, 0.651);*/
	}

	100% {
		transform: scale(0.90);
	    /*	box-shadow: 0 0 0 0  rgba(233, 157, 16, 0.651);*/
	}
}


/****** TYPE WRITTER ***/
.typewriter h4 {
    overflow: hidden; /* Ensures the content is not revealed until the animation */
    border-right: .15em solid orange; /* The typwriter cursor */
    white-space: nowrap; /* Keeps the content on a single line */
    margin: 0 auto; /* Gives that scrolling effect as the typing happens */
    letter-spacing: .15em; /* Adjust as needed */
    width:auto;
    display:inline;
    animation: 
      typing 3.5s steps(40, end),
      blink-caret .75s step-end infinite;
    
  }
  
  /* The typing effect */
  @keyframes typing {
    from { width: 0 }
    to { width: 100% }
  }
  
  /* The typewriter cursor effect */
  @keyframes blink-caret {
    from, to { border-color: transparent }
    50% { border-color: orange; }
  }

